.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

button {
  height: 25px;
  border: solid rgb(120, 120, 100) 2px;
  box-shadow: 1px 2px 8px -2px black;
  width: 90px;
  margin: 5px;
}

.cardWrapper {
  display: grid;
  grid-template-columns: repeat(4, 30px) 140px;
  border: solid rgb(120, 120, 100) 2px;
  box-shadow: 1px 2px 8px -2px black;
  max-width: 217px;
}
